
body {
  font-family: Helvetica, sans-serif;
}
*{
  margin:0;
  padding:0;
}
#app {
padding:20px;
  width:80%;
  margin: 30px auto;
  border:1px solid #ddd;
}
body h2{
  text-align:center;
}
#add-input {
  width: 95%;
  height: 35px;
  padding: 0 5px;
  margin:10px auto;
  display:block;
  box-sizing:border-box;
}
ul {
  list-style: none;
  padding: 0;
}
ul li{
  padding:10px;
  width:90%;
  box-sizing:border-box;
  margin:0 auto;
}
h3{
  font-size:18px;
  font-weight:500;
}



